  padding-bottom: 15px;
}

@media only screen and (max-width: 360px) {
  .footer__go-top {
    font-size: 15px;
  }
}

.footer__go-top br {
  display: none;
}

@media only screen and (max-width: 700px) {
  .footer__go-top br {
    display: block;
  }
}

.footer__buttons {
  text-align: center;
  min-height: 80px;
  border-bottom: 1px solid #eaebef;
}

.footer__buttons div {
  height: 100%;
  float: left;
}

@media only screen and (max-width: 730px) {
  .footer__buttons div {
    float: none;
    width: 100%;
  }
}

.footer__buttons .icon-logo {
  fill: #4d5470;
  font-size: 46px;
}

.footer__logo {
  padding-top: 17px;
  width: 25%;
  border-right: 1px solid #eaebef;
}

.footer__call-back {
  width: 50%;
}

.footer__call-back a {
  padding-top: 3px;
  display: block;
  margin: 20px auto 0;
}

.footer__socials {
  width: 25%;
  border-left: 1px solid #eaebef;
}

@media only screen and (max-width: 730px) {
  .footer__socials {
    padding-bottom: 15px;
  }
}

.footer__socials a {
  display: inline-block;
  margin: 30px 12px 0;
}

.footer__socials .icon {
  font-size: 20px;
  fill: #a1a7be;
  transition: all 0.3s ease;
}

.footer__socials .icon:hover {
  fill: #283452;
}

.footer__socials .icon-vk {
  font-size: 16px;
}

.footer__copy {
  min-height: 64px;
  text-align: center;
  padding-top: 20px;
}

.footer__copy p {
  font-size: 14px;
  color: #283452;
  opacity: 0.5;
  letter-spacing: 1px;
  font-family: "MyriadProRegular", sans-serif;
}

.footer__copy p span {
  font-family: "GothamProMedium", sans-serif;
}

.mod {
  max-width: 750px;
  min-height: 360px;
  margin: 0 auto;
}

@media only screen and (max-width: 550px) {
  .mod {
    max-width: 360px;
  }
}

.mod__content {
  max-width: 750px;
  min-height: 360px;
  position: relative;
  border-radius: 8px;
  margin-top: 20px;
  box-shadow: 0px 10px 29px 0px rgba(0, 0, 0, 0.4);
}

@media only screen and (max-width: 480px) {
  .mod__content {
    margin-top: 120px;
  }
}

.mod__form, .mod__image {
  min-height: 360px;
  width: 50%;
  float: left;
}

.mod__form {
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 8px 0 0 8px;
  padding: 22px 28px;
}

@media only screen and (max-width: 767px) {
  .mod__form {
    padding: 15px;
  }
}

@media only screen and (max-width: 550px) {
  .mod__form {
    border-radius: 8px;
    width: 100%;
  }
}

.mod__title {
  font-size: 36px;
  color: #fbf7f7;
  line-height: 1;
  margin-bottom: 30px;
}

@media only screen and (max-width: 767px) {
  .mod__title {
    font-size: 30px;
  }
}

@media only screen and (max-width: 550px) {
  .mod__title {
    font-size: 36px;
    max-width: 280px;
    margin: 0 auto 30px;
    text-align: center;
  }
}

.mod__title span:first-child {
  font-family: "GothamProMedium", sans-serif;
}

.mod__title span:nth-child(2) {
  color: #ff8484;
  font-family: "GothamProMedium", sans-serif;
}

.mod__title span:last-child {
  font-size: 28px;
  font-family: "GothamProLight", sans-serif;
}

.mod__fields {
  max-width: 239px;
}

@media only screen and (max-width: 550px) {
  .mod__fields {
    margin: 0 auto;
  }
}

.mod__fields .name_user::after {
  width: 130px;
  right: 2px;
  top: 1px;
}

.mod__fields .tel_num::after {
  width: 34px;
  right: 2px;
  top: 1px;
}

.mod__fields label {
  width: 100%;
  height: 40px;
  margin-bottom: 20px;
  position: relative;
  border-radius: 8px 8px 6px 6px;
  border: 1px solid #fff;
  border-top-color: transparent;
}

.mod__fields label::before, .mod__fields label::after {
  position: absolute;
  content: '';
  height: 1px;
  background-color: #fff;
  transition: all 0.2s ease;
}

.mod__fields label::before {
  left: 2px;
  top: 1px;
  width: 7px;
}

.mod__fields label span {
  position: absolute;
  left: 16px;
  top: -10px;
  font-family: "GothamProLight", sans-serif;
  font-size: 16px;
  color: #ffc9c1;
  transition: all 0.2s ease;
}

.mod__fields .move_label::after {
  width: 233px !important;
}

.mod__fields .move_label span {
  font-size: 14px;
  color: #fff;
  transform: translateY(-8px) !important;
}

.mod__fields input {
  width: 100%;
  height: 40px;
  background-color: transparent;
  color: #fff;
  padding: 5px 10px 0;
  font-family: "GothamProLight", sans-serif;
  font-size: 16px;
  border-radius: 5px;
  transition: all 0.2s ease;
}

.mod__fields input:focus {
  background-color: rgba(255, 255, 255, 0.1) !important;
}

.mod__image {
  background-color: rgba(48, 48, 48, 0.7);
  position: relative;
  border-radius: 0 8px 8px 0;
}

@media only screen and (max-width: 550px) {
  .mod__image {
    display: none;
  }
}

.mod__image img {
  position: absolute;
  bottom: 0;
  right: 0;
  display: block;
  max-width: 100%;
}

.mod__closer {
  position: absolute;
  right: -50px;
  top: -60px;
  width: 50px;
  height: 50px;
  border-radius: 14px;
  background-color: #e83360;
  cursor: pointer;
}

@media only screen and (max-width: 900px) {
  .mod__closer {
    right: 0;
  }
}

.mod__closer:hover .mod__closer--lines::after {
  transform: rotate(225deg);
}

.mod__closer:hover .mod__closer--lines::before {
  transform: rotate(315deg);
}

.mod__closer--lines {
  width: 34px;
  height: 34px;
  margin: 24px auto 0;
  position: relative;
}

.mod__closer--lines::before, .mod__closer--lines::after {
  transition: all 0.15s linear;
  position: absolute;
  content: '';
  width: 34px;
  height: 1px;
  background-color: #fff;
  box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
}

.mod__closer--lines::after {
  left: 0;
  top: 0;
  transform: rotate(45deg);
}

.mod__closer--lines::before {
  left: 0;
  top: 0;
  transform: rotate(135deg);
}

